import recommended from "../../components/recommended.vue";
export default {
	components: {
		recommended
	},
	data() {
		return {
			details: [],
			comment: [],
			address: [],
			inx:0,
			display: "none",
			topa: "hide",
			topb: "hide",
			topc: "hide"
		}
	},
	onLoad(id) {
		// uni.request({
		// 	url: 'http://ceshi3.dishait.cn/api/login',
		// 	method: 'POST',
		// 	data: {
		// 		username: 'user2',
		// 		password: 'zcmcss'
		// 	},
		// 	success: (res) => {
		// 		console.log(res.data.data.token)
		// 	}
		// });
		uni.request({
			url: 'http://ceshi3.dishait.cn/api/useraddresses/1',
			header: {
				'token': 'f74000a8dd9770986e812dcfd1ffb463d3dc0d4b'
			},
			success: (res) => {
				this.address = res.data.data
				console.log(this.address)
			}
		});
		let ids = id.id
		uni.request({
			url: 'http://ceshi3.dishait.cn/api/goods/' + ids,
			success: (res) => {
				this.details = res.data.data;
				console.log('详情', ids, this.details)
			}
		});
		uni.request({
			url: 'http://ceshi3.dishait.cn/api/goods/' + ids + '/comments/good?page=1',
			success: (res) => {
				this.comment = res.data.data
				console.log(ids, this.comment)
			}
		});
		if(this.comment.list==''){
			this.comment.list=this.details.hotComments
		}
	},
	methods: {
		shipping(i){
			this.inx=i
			this.display = "none";
			this.topa = "hide"
			this.topb = "hide"
			this.topc = "hide"
		},
		back() {
			uni.navigateBack(1)
		},
		cart() {
			uni.switchTab({
				url: '/pages/shopping/shopping'
			})
		},
		show() {
			this.display = "none";
			this.topa = "hide"
			this.topb = "hide"
			this.topc = "hide"
		},
		showA() {
			this.display = "block";
			this.topa = "show"
		},
		showB() {
			this.display = "block";
			this.topb = "show"
		},
		showC() {
			this.display = "block";
			this.topc = "show"
		}
	}
}
